<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      setting: {},
      options: [
        {
          value: 'money',
          label: '现金',
        },
        {
          value: 'exchange_credit',
          label: '兑换积分',
        },
      ],
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/inforeward', {
      type: 'reward',
    })
      .then(r => {
        this.loading = false
        this.setting = r.data
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch3('/sys/config/inforeward', {
        type: 'reward',
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
  },
}
</script>
